<template>
  <div class="container">
    <h3>정보입력</h3>
    <form v-on:submit.prevent="submitForm" action="memberInsert">
      <div class="name">
        <label for="name">이름</label>
        <input type="text" v-model="userName" placeholder="이름을 입력해주세요" required />
      </div>
      <hr />
      <div class="id">
        <label for="id">아이디</label>
        <input type="text" v-model="userId" placeholder="아이디를 입력해주세요" required />
        <button type="button" @click="checkUserId">중복확인</button>
        <span v-if="userIdChecked && userIdExists">이미 사용 중인 아이디입니다.</span>
        <span v-if="userIdChecked && !userIdExists">사용 가능한 아이디입니다.</span>
      </div>
      <hr />
      <div class="pw">
        <label for="password">비밀번호</label>
        <input type="password" v-model="userPw" placeholder="비밀번호를 입력해주세요" required />
        <label>영문대소문자/숫자/특수문자 중 2가지 이상 조합,10자~16자</label>
      </div>
      <div class="pwfirm">
        <label>비밀번호 확인</label>
        <input type="password" v-model="passwordConfirm" @change="pwMatchCheck" placeholder="비밀번호를 다시 입력해주세요"
          required />
        <span id="pwCheckConfirm" :class="{ 'match': passwordsMatch, 'not-match': !passwordsMatch }"> {{
      passwordMatchMessage }}</span>
      </div>
      <hr />
      <div class="phone">
        <label>휴대전화</label>
        <select id="mobile1" v-model="mobile1" name="mobile" label="mobile">
          <option value="010">010</option>
          <option value="011">011</option>
          <option value="016">016</option>
          <option value="017">017</option>
          <option value="018">018</option>
          <option value="019">019</option>
        </select>
        -
        <input id="mobile2" v-model="mobile2" name="mobile[]" maxlength="4" fw-filter="isNumber&amp;isFill"
          fw-label="휴대전화" fw-alone="N" placeholder="" value="" type="text" />
        -
        <input id="mobile3" v-model="mobile3" name="mobile[]" maxlength="4" fw-filter="isNumber&amp;isFill"
          fw-label="휴대전화" fw-alone="N" placeholder="" value="" type="text" />
      </div>
      <hr />
      <div class="addressbox">
        <div class="address">
          <label>주소</label>
          <input type="text" id="postcode" v-model="userPostCode" placeholder="우편번호" />
          <input type="button" style="width: 80px; height: 30px; font-weight: 300;" @click="search()"
            value="주소검색" /><br /><br />
          <input type="text" id="roadAddress" v-model="userRoadAddress" placeholder="도로명주소" />
          <input type="text" id="jibunAddress" v-model="userJibunAddress" placeholder="지번주소" />
          <span id="guide" style="color: #000; display: none"></span>
          <input type="text" id="detailAddress" v-model="userDetailAddress" placeholder="상세주소" />
          <input type="text" id="extraAddress" v-model="userExtraAddress" placeholder="참고항목" />
        </div>
      </div>
      <h3>약관동의</h3>
      <div class="checkbox">
        <div class="form-check">
          <input class="form-check-input" type="checkbox" v-model="allAgreed" @change="checkAllAgreement"
            id="flexCheckDefault" />
          <label class="form-check-label" for="flexCheckDefault">
            모든 약관 동의
          </label>
        </div>
        <hr />
        <div class="form-check" v-for="(agreement, index) in agreements" :key="index">
          <input class="form-check-input" type="checkbox" v-model="agreement.agreed" :id="'flexCheckChecked' + index" />
          <label class="form-check-label" :for="'flexCheckChecked' + index">
            {{ agreement.label }}
          </label>
        </div>
        <hr />
        <p>주문 내용을 확인했으며 약관에 동의합니다.</p>
      </div>
      <button type="submit" class="btn" :disabled="!allAgreed">✓ 회원가입</button>
    </form>
  </div>
</template>

<script>
import axios from 'axios'; // axios를 import 합니다.
import router from '@/router'; // Vue Router를 import 합니다.

const serverUrl = 'http://localhost:80'
export default {
  name: "AddressRoad",

  data() {
    return {
      userId: '',
      userPw: '',
      passwordConfirm: '',
      userPostCode: '',
      userName: '',
      userRoadAddress: '',
      userJibunAddress: '',
      userDetailAddress: '',
      userExtraAddress: '',
      mobile1: '',
      mobile2: '',
      mobile3: '',
      mobile: '',
      userIdChecked: true, // 아이디 중복확인 여부
      userIdExists: false, // 아이디 중복 여부
      agreements: [
        { label: '[필수] 쇼핑몰 이용약관', agreed: false },
        { label: '[필수] 개인정보 수집 및 이용 동의', agreed: false },
        { label: '[필수] 청약철회방침 동의', agreed: false }
      ]
    };
  },
  computed: {
    passwordMatchMessage() {
      return this.passwordsMatch ? '일치합니다.' : '일치하지 않습니다.';
    }
  },
  methods: {

    submitForm() {
      if (!this.userIdChecked || this.userIdExists) {
        alert("아이디 중복을 확인해주세요.");
        return;
      }

      const postData = {
        userId: this.userId,
        userPw: this.userPw,
        userPostCode: this.userPostCode, // 주소 정보 추가
        userRoadAddress: this.userRoadAddress, // 주소 정보 추가
        userDetailAddress: this.userDetailAddress,
        userJibunAddress: this.userJibunAddress,
        userExtraAddress: this.userExtraAddress,
        userName: this.userName,
        userMobile: this.mobile1 + this.mobile2 + this.mobile3,
      }
      alert(this.userName + '님의 회원가입을 시작합니다!!!')
      axios
        .post(serverUrl + '/member/memberInsert', postData) // axios를 사용하여 데이터를 전송합니다.
        .then(response => {
          console.log(response);
          alert('회원가입이 완료되었습니다.');
          router.push({ name: 'login' }); // 회원가입 후 로그인 페이지로 이동합니다.
        })
        .catch(error => {
          alert('회원가입 실패');
          console.error(error);
        });
      // alert(postData)
    },
    // 아이디 중복확인
    checkUserId() {
      if (this.userId) {
        axios.get(serverUrl + '/member/checkUserId', { params: { userId: this.userId } })
          .then(response => {
            this.userIdChecked = true;
            this.userIdExists = response.data.exists;
          })
          .catch(error => {
            console.error(error);
          });
      } else {
        alert("아이디를 입력해주세요.");
      }
    },
    search() {
      new window.daum.Postcode({
        oncomplete: (data) => {
          var roadAddr = data.roadAddress; // 도로명 주소 변수
          var extraRoadAddr = ""; // 참고 항목 변수

          if (data.bname !== "" && /[동|로|가]$/g.test(data.bname)) {
            extraRoadAddr += data.bname;
          }
          // 건물명이 있고, 공동주택일 경우 추가한다.
          if (data.buildingName !== "" && data.apartment === "Y") {
            extraRoadAddr +=
              extraRoadAddr !== ""
                ? ", " + data.buildingName
                : data.buildingName;
          }
          // 표시할 참고항목이 있을 경우, 괄호까지 추가한 최종 문자열을 만든다.
          if (extraRoadAddr !== "") {
            extraRoadAddr = " (" + extraRoadAddr + ")";
          }

          // 우편번호와 주소 정보를 해당 필드에 넣는다.
          this.userPostCode = data.zonecode;
          this.userRoadAddress = roadAddr;
          this.userJibunAddress = data.jibunAddress;

          // 참고항목 문자열이 있을 경우 해당 필드에 넣는다.
          if (roadAddr !== "") {
            this.userExtraAddress = extraRoadAddr;
          } else {
            this.userExtraAddress = "";
          }

          var guideTextBox = document.getElementById("guide");
          // 사용자가 '선택 안함'을 클릭한 경우, 예상 주소라는 표시를 해준다.
          if (data.autoRoadAddress) {
            var expRoadAddr = data.autoRoadAddress + extraRoadAddr;
            guideTextBox.innerHTML = "(예상 도로명 주소 : " + expRoadAddr + ")";
            guideTextBox.style.display = "block";
          } else if (data.autoJibunAddress) {
            var expJibunAddr = data.autoJibunAddress;
            guideTextBox.innerHTML = "(예상 지번 주소 : " + expJibunAddr + ")";
            guideTextBox.style.display = "block";
          } else {
            guideTextBox.innerHTML = "";
            guideTextBox.style.display = "none";
          }
        }
      }).open();
    },

    pwMatchCheck() {
      if (this.passwordConfirm == this.userPw && this.userPw != '') {
        document.getElementById("pwCheckConfirm").innerText = "일치합니다.";
      }
      else {
        document.getElementById("pwCheckConfirm").innerText = "일치하지 않습니다.";
      }
    },
    checkAllAgreement() {
      for (const agreement of this.agreements) {
        agreement.agreed = this.allAgreed;
      }
    }
  }
};
</script>


