<template>
  <div class="container">
    <div class="textGroup">
      <h2>비밀번호 찾기</h2>
    </div>
    <div class="memberType">
      <p>회원유형</p>
      <select class="form-select custom-form-control-secondary" aria-label="Default select example">
        <option selected>개인회원</option>
        <option value="1">법인회원</option>
      </select>
    </div>
    <form @submit.prevent="findUserPw">
      <div class="input-group mb-3">
        <p>아이디</p>
        <input type="text" class="form-control custom-form-control-secondary rounded" aria-label="Userid"
          v-model="userId" :disabled="isVerificationCodeVisible" />
      </div>
      <div class="input-group mb-3">
        <p>이름</p>
        <input type="text" class="form-control custom-form-control-secondary rounded" aria-label="Username"
          v-model="userName" :disabled="isVerificationCodeVisible" />
      </div>
      <div class="call">
        <div action="/action_page.php" class="callbox">
          <label for="phone">전화번호</label>
          <input type="tel" id="phone" name="phone" placeholder="Ex) 0101234567" pattern="[0-9]{2,3}[0-9]{3,4}[0-9]{4}"
            required v-model="userMobile" :disabled="isVerificationCodeVisible" />
        </div>
      </div>
      <!-- <div id="verification" class="d-flex flex-column">
        <button class="code" @click.prevent="requestVerificationCode" :disabled="isVerificationCodeVisible">코드
          전송</button>
        <div class="d-flex" v-if="isVerificationCodeVisible">
          <input class="borderbox" type="text" v-model="verificationCode" placeholder="인증코드">
          <button class="btn btn-info btn-sm justify-content-center">인증코드확인</button>
        </div>
      </div> -->
      <!-- <router-link :to="{ name: 'Passwordchange', params: { userId, userName, userMobile } }" class="btn btn-outline-secondary" id="button-addon1">
        확인
      </router-link> -->
      <button class="btn btn-outline-secondary" type="submit" id="button-addon1">
        확인
      </button>
    </form>
  </div>
</template>

<script>
import axios from 'axios';// axios를 import 함
//import router from '@router'; // router.push를 사용해서 비밀번호 변경
//페이지로 넘겨버릴거임

const serverUrl = 'http://localhost:80'  //백엔드 서버로 보낼 예정
export default {
  data() {
    return {
      // verificationCode: '',//임시 인증번호를 입력시 아이디를 보여줄 수 있게 코드를 작성
      userId: '',
      userMobile: '',
      userName: '',
      //isVerificationCodeVisible: false,
    }
  },
  methods: {
    findUserPw() {
      var userData = {
        userName: this.userName,
        userId: this.userId,
        userMobile: this.userMobile,
      }

      // this.isVerificationCodeVisible = true;
      axios.post(serverUrl + '/member/find-user-pw', userData)
        .then(response => {
          if (response.data === "success") {
            alert('비밀번호변경 페이지로 이동합니다');
            console.log(userData)
            this.$store.commit('setUserData', userData)
            // 비밀번호 변경 페이지로 이동하면서 데이터 전달
            this.$router.push('passwordchange');
          } else {
            alert("잘못된 회원정보를 입력하셨습니다");
          }
        }).catch(error => {
          console.error('Error finding user ID:', error)
        });


    }
  }
};
</script>

