import { createRouter, createWebHistory } from "vue-router";
import store from '@/store'
import AdminDashboard from "../components/admin/AdminDashboard.vue";
import AdminManagement from "../components/admin/AdminManagement.vue";
import AdminDetailedPage from "@/components/admin/AdminDetailedPage.vue";
import AdminOrder from "@/components/admin/AdminOrder.vue";
import AdminProduct from "@/components/admin/AdminProduct.vue";
import HomeView from "../views/HomeView.vue";
import CustomerServiceView from "../views/CustomerServiceView.vue";
import LoginView from "../views/LoginView.vue";
import IdRecover from "../views/IdRecover.vue";
import PwRecover from "../views/PwRecover.vue";
import JoinMember from "../views/member/JoinMember.vue";
import AppMyShop from "../views/AppMyShop.vue";
import MemberInformation from "../views/MemberInformation.vue";
import ReviewView from '../views/ReviewView.vue'
import DetailedPage from '../views/DetailedPage.vue'
import ShoppingCart from '../views/ShoppingCartView.vue'
import ProductBoard from '@/views/ProductBoard.vue'
import TheOrderform from '../views/TheOrderform.vue'
import MemberList from '../views/member/MemberList.vue'

import PasswordChange from "@/views/PasswordChange.vue";
import EditMember from "@/views/EditMember.vue";


const routes = [
  {
    path: "/",
    name: "home",
    component: HomeView,
  },
  {
    path: '/category/:category',

    name: 'product',
    props: true,
    component: ProductBoard
  },
  {
    path: "/customerservice",
    name: "customerService",
    component: CustomerServiceView,
  },
  {
    path: "/login",
    name: "login",
    component: LoginView,
  },
  {
    path: "/idrecover",
    name: "idrecover",
    component: IdRecover,
  },
  {
    path: "/pwrecover",
    name: "pwrecover",
    component: PwRecover,
  },
  {
    path: "/member/join",
    name: "joinmember",
    component: JoinMember,
  },
  {
    path: "/appmyshop",
    name: "appmyshop",
    component: AppMyShop,
  },
  {
    path: "/memberinformation",
    name: "memberinformation",
    component: MemberInformation,
  },
  {
    path: '/review',
    name: 'review',
    component: ReviewView
  },
  {
    path: '/detailed',
    name: 'detailed',
    component: DetailedPage
  },
  {
    path: '/theorder',
    name: 'theorder',
    component: TheOrderform
  },
  {
    path: '/shoppingcart',
    name: 'shoppingcart',
    component: ShoppingCart
  },
  {
    
    path: '/passwordchange',
    name: 'Passwordchange',
    component: PasswordChange
  },
  {
    
    path: '/editMember',
    name: 'EditMember',
    component: EditMember
    },
    {
    path: '/admin',
    name: 'AdminDashboard',
    component: AdminDashboard
  },
  {
    path: '/admin/detailed',
    name: 'AdminDetailedPage',
    component: AdminDetailedPage
  },
  {
    path: '/admin/management',
    name: 'AdminManagement',
    component: AdminManagement
  },
  {
    path: '/admin/product',
    name: 'AdminProduct',
    component: AdminProduct
  },
  {
    path: '/admin/order',
    name: 'AdminOrder',
    component: AdminOrder
  }
  // ,{
  //    path: '/logout', // 로그아웃 경로 추가
  //     name: 'logout', // 로그아웃 이름 추가
  //     meta: { requiresAuth: true }, // 인증이 필요한 경로로 지정
  //     beforeEnter: (to, from, next) => { // beforeEnter 가드 추가
  //       // 로그아웃 요청 처리
  //       // axios 또는 fetch 등을 사용하여 서버에 로그아웃 요청을 보낼 수 있음
  //       next('/');
  // }}
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
