<template>
  <div class="container">
    <h3>정보입력</h3>
    <form v-on:submit.prevent="submitForm">
      <div class="name">
        <label for="name">이름</label>
        <input type="text" v-model="userName" :disabled="true" >
      </div>
      <hr />
      <div class="id">
        <label for="id">아이디</label>
        <input type="text" v-model="userId" :disabled="true" />

      </div>
      <hr />
      <div class="pw">
        <label for="password">비밀번호</label>
        <input type="password" v-model="userPw" placeholder="비밀번호를 입력해주세요" />
        <label>영문대소문자/숫자/특수문자 중 2가지 이상 조합,10자~16자</label>
      </div>
      <div class="pwfirm">
        <label>비밀번호 확인</label>
        <input type="password" v-model="passwordConfirm" @change="pwMatchCheck" placeholder="비밀번호를 입력해주세요" />
        <span id="pwCheckConfirm"></span>
      </div>
      <hr />
      <div class="phone">
        <label>휴대전화</label>
        <input id="mobile1" v-model="mobile1" name="mobile" label="mobile" />
        -
        <input id="mobile2" v-model="mobile2" name="mobile[]" maxlength="4" fw-filter="isNumber&amp;isFill"
          fw-label="휴대전화" fw-alone="N" placeholder="" value="" type="text" />
        -
        <input id="mobile3" v-model="mobile3" name="mobile[]" maxlength="4" fw-filter="isNumber&amp;isFill"
          fw-label="휴대전화" fw-alone="N" placeholder="" value="" type="text" />
      </div>
      <hr />
      <div class="addressbox">
        <div class="address">
          <label>주소</label>
          <input type="text" id="postcode" v-model="userPostCode" placeholder="우편번호" />
          <input type="button" style="width: 80px; height: 30px; font-weight: 300;" @click="search()"
            value="주소검색" /><br /><br />
          <input type="text" id="roadAddress" v-model="userRoadAddress" placeholder="도로명주소" />
          <input type="text" id="jibunAddress" v-model="userJibunAddress" placeholder="지번주소" />
          <span id="guide" style="color: #000; display: none"></span>
          <input type="text" id="detailAddress" v-model="userDetailAddress" placeholder="상세주소" />
          <input type="text" id="extraAddress" v-model="userExtraAddress" placeholder="참고항목" />
        </div>
      </div>
      <button type="submit" class="btn">✓ 수정완료</button>
    </form>
  </div>
</template>

<script>
import store from '@/store';
import axios from 'axios'; // axios를 import 합니다.
import router from '@/router'; // Vue Router를 import 합니다.
const serverUrl = 'http://localhost:80'
export default {
  name: "AddressRoad",
  data() {
    return {
      userId: '',
      userPw: '',
      passwordConfirm: '',
      userPostCode: '',
      userName: '',
      userRoadAddress: '',
      userJibunAddress: '',
      userDetailAddress: '',
      userExtraAddress: '',
      mobile1: '',
      mobile2: '',
      mobile3: '',
      userMobile: ''
    };
  },
  mounted() {
    this.userId = store.state.account.userId

    axios.get(serverUrl + '/member/memberEdit', { params: { userId: this.userId } })
      .then(response => {
        this.userName = response.data.userName;
        var mobile = response.data.userMobile;
        this.mobile1 = mobile.substring(0,3);
        this.mobile2 = mobile.substring(3,7);
        this.mobile3 = mobile.substring(7);
        this.userPostCode = response.data.userPostCode;
        this.userRoadAddress = response.data.userRoadAddress;
        this.userJibunAddress = response.data.userJibunAddress;
        this.userDetailAddress = response.data.userDetailAddress;
        this.userExtraAddress = response.data.userExtraAddress;
      })
      .catch(error => {
        alert('회원정보수정 mounted 실패');
        console.error(error);
      });
  },
  methods: {
    
    submitForm() {
      

      const postData = {
        userId: this.userId,
        userPw: this.userPw,
        userPostCode: this.userPostCode, // 주소 정보 추가
        userRoadAddress: this.userRoadAddress, // 주소 정보 추가
        userDetailAddress: this.userDetailAddress,
        userJibunAddress: this.userJibunAddress,
        userExtraAddress: this.userExtraAddress,
        userName: this.userName,
        userMobile: this.mobile1 + this.mobile2 + this.mobile3,
      }
      alert(this.userName + '님의 회원수정을 시작합니다!!!')
      axios
        .put(serverUrl + '/member/memberUpdate', postData) // axios를 사용하여 데이터를 전송합니다.
        .then(response => {
          console.log(response);
          alert('회원수정이 완료되었습니다.');
          router.push({ name: 'login' }); // 회원가입 후 로그인 페이지로 이동합니다.
        })
        .catch(error => {
          alert('회원수정 실패');
          console.error(error);
        });
      // alert(postData)
    },
    search() {
      new window.daum.Postcode({
        oncomplete: (data) => {
          var roadAddr = data.roadAddress; // 도로명 주소 변수
          var extraRoadAddr = ""; // 참고 항목 변수

          if (data.bname !== "" && /[동|로|가]$/g.test(data.bname)) {
            extraRoadAddr += data.bname;
          }
          // 건물명이 있고, 공동주택일 경우 추가한다.
          if (data.buildingName !== "" && data.apartment === "Y") {
            extraRoadAddr +=
              extraRoadAddr !== ""
                ? ", " + data.buildingName
                : data.buildingName;
          }
          // 표시할 참고항목이 있을 경우, 괄호까지 추가한 최종 문자열을 만든다.
          if (extraRoadAddr !== "") {
            extraRoadAddr = " (" + extraRoadAddr + ")";
          }

          // 우편번호와 주소 정보를 해당 필드에 넣는다.
          this.userPostCode = data.zonecode;
          this.userRoadAddress = roadAddr;
          this.userJibunAddress = data.jibunAddress;

          // 참고항목 문자열이 있을 경우 해당 필드에 넣는다.
          if (roadAddr !== "") {
            this.userExtraAddress = extraRoadAddr;
          } else {
            this.userExtraAddress = "";
          }

          var guideTextBox = document.getElementById("guide");
          // 사용자가 '선택 안함'을 클릭한 경우, 예상 주소라는 표시를 해준다.
          if (data.autoRoadAddress) {
            var expRoadAddr = data.autoRoadAddress + extraRoadAddr;
            guideTextBox.innerHTML = "(예상 도로명 주소 : " + expRoadAddr + ")";
            guideTextBox.style.display = "block";
          } else if (data.autoJibunAddress) {
            var expJibunAddr = data.autoJibunAddress;
            guideTextBox.innerHTML = "(예상 지번 주소 : " + expJibunAddr + ")";
            guideTextBox.style.display = "block";
          } else {
            guideTextBox.innerHTML = "";
            guideTextBox.style.display = "none";
          }
        }
      }).open();
    },

    pwMatchCheck() {
      if (this.passwordConfirm == this.userPw && this.userPw != '') {
        document.getElementById("pwCheckConfirm").innerText = "일치합니다.";
      }
      else {
        document.getElementById("pwCheckConfirm").innerText = "일치하지 않습니다.";
      }
    }
  }
};
</script>


