<template>
  <div class="container">
    <div class="textGroup">
      <h2>비밀번호 변경</h2>
    </div>
    <form @submit.prevent="changePassword">
      <div class="input-group mb-3">
        <p>새 비밀번호</p>
        <input type="password" class="form-control custom-form-control-secondary rounded" aria-label="New Password"
          v-model="userPw" />
      </div>
      <div class="input-group mb-3">
        <p>새 비밀번호 확인</p>
        <input type="password" class="form-control custom-form-control-secondary rounded"
          aria-label="Confirm New Password" v-model="confirmNewPassword" />
      </div>
      <p> {{$store.state.userData.userId}}
       </p>
      <button class="btn btn-outline-secondary" type="submit">
        변경
      </button>
    </form>
  </div>
</template>

<script>
import axios from 'axios';
import store from '@/store';
import router from '@/router';
const serverUrl = 'http://localhost:80';
export default {
  pwdata() {
    return {
      userPw: '',
      confirmNewPassword: '',
    }
  },

  methods: {
    changePassword() {
      if (this.userPw !== this.confirmNewPassword) {
        alert('새 비밀번호와 확인 비밀번호가 일치하지 않습니다.');
        return;
      }
      var data = {
        
        userId: store.state.userData.userId,
        userName: store.state.userData.userName,
        userMobile: store.state.userData.userMobile,
        userPw: this.userPw,
      }
      console.log(data);
      axios.put(serverUrl + '/member/change-password', data)
        .then(response => {
          if (response.data === "success") {
            alert('비밀번호가 성공적으로 변경되었습니다.');
              store.commit('setUserData', null)
              //localStorage.removeItem("sessionId")
              router.push({path:'/login'})
          } else {
            alert('비밀번호 변경에 실패했습니다.');
          }
        })
        .catch(error => {
          console.error('Error changing password:', error);
          alert('비밀번호 변경 중 오류가 발생했습니다.');
        });
    }
  }
};
</script>

