<template>
  <div class="container">

    <div class="textGroup">
      <h2>아이디 찾기</h2>
      <p>
        · 가입하신 방법에 따라 아이디 찾기가 가능합니다.
        <br />
        · 법인사업자 회원 또는 외국인 회원의 경우 법인명과 법인번호 또는 이름과
        등록번호를 입력해 주세요.
      </p>
    </div>
    <div class="memberType">
      <p>회원유형</p>
      <select class="form-select custom-form-control-secondary" aria-label="Default select example">
        <option selected>개인회원</option>
        <option value="1">법인회원</option>
      </select>
    </div>



    <form @submit.prevent="findUserId">
      <div class="input-group mb-3">
        <p>이름</p>
        <input type="text" class="form-control custom-form-control-secondary rounded" v-model="userName"
          aria-label="Username" required :disabled="isVerificationCodeVisible" />
      </div>
      <div class="call">
        <div class="callbox">
          <div class="one" v-if="callNumber == 'call'">
            <label for="phone">전화번호</label>
            <input type="tel" id="phone" v-model="userMobile" class="form-control custom-form-control-secondary"
              name="phone" placeholder="예: 010-1234-5678" pattern="[0-9]{2,3}[0-9]{3,4}[0-9]{4}" required :disabled="isVerificationCodeVisible" />
          </div>

        </div>
      </div>
      <div id="verification" class="d-flex flex-column">
        <button class="code" @click.prevent="requestVerificationCode" :disabled="isVerificationCodeVisible">코드 전송</button>
        <div class="d-flex" v-if="isVerificationCodeVisible">
          <input class="borderbox" type="text" v-model="verificationCode" placeholder="인증코드">  
          <button class="btn btn-info btn-sm justify-content-center">인증코드확인</button>
        </div>
      </div>
      <button class="btn btn-outline-secondary" type="submit" id="button-addon1"> 확인</button>
    </form>
  </div>
</template>



<script>

import axios from 'axios'; // axios를 import 합니다.
const serverUrl = 'http://localhost:80'

export default {
  data() {
    return {
      verificationCode: '',//임시 인증번호를 입력시 아이디를 보여줄 수 있게 코드를 작성
      callNumber: 'call',
      userMobile: '',
      userName: '',
      isVerificationCodeVisible:false,
    };
  },
  methods: {
    requestVerificationCode() {
      //코드 전송을 위해 사용자의 이름과 전화번호를 서버에 보낸다.
      if(this.userName == "" || this.userMobile == ""){
        // 이름과 전화번호를 입력하지 않은 경우 알림을 띄웁니다.
        alert("이름과 전화번호를 입력해주세요")
      }else{
        var data = {
        userName: this.userName,
        userMobile: this.userMobile

      }
      
      axios.post(serverUrl + '/member/checkInfo', data)
        .then(response => {
          if (response.data) {
            alert('인증번호가 발송되었습니다')
            this.isVerificationCodeVisible = true;
          } else {
            alert('등록된 회원 정보가 없습니다.')
          }
        })
        .catch(error => {
          console.error('에러 발생', error);
        })
    }
      }

      
    ,
    findUserId() {
      var data = {
        userName: this.userName,
        userMobile: this.userMobile

      }
      if (this.verificationCode) {

        //백엔드에 설정해둔 인증 코드를 사용하여 사용자 아이디를 찾는다
        axios.post(serverUrl + '/member/find-user-id', data)
          .then(response => {
            alert(`사용자 아이디: ${response.data}`);
          })
          .catch(error => {
            console.error('Error finding user ID:', error);
          });
      } else {
        // 고정된 인증번호가 아니라면 오류 메시지를 표시합니다.
        alert('잘못된 인증번호입니다.');
      }
    }
  }
};
</script>

